@import url(https://fonts.googleapis.com/css2?family=Roboto:ital@1&display=swap);
.CryptoApp{
  width:100%;
  height:100%;
  justify-content: center;
}
#preloaderDIV{
  width: 100%;
  height:100vh;
  align-items: center;
  justify-content: center;
  display: flex;
  align-items: center;
}
html, body{
  min-height: 100%;
}
*{
  box-sizing: border-box;
  margin:0;
  padding:0;
  font-family: 'Roboto', sans-serif;
}
.theme-dark {
  margin:0;
  background-color: #1a1a1c;
  background: #1a1a1c;
  color:#fff;
}
#coin-div{
  height:100%;
}
.theme-light{
  background-color: #fff;
  background: #fff;
  color:#1a1a1c;
}
.coin-app{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;
}

.search-bar{
  margin-bottom: 5vh;
  display: flex;
  text-align: center;
  justify-content: space-around;
  align-items: center;
  height:10vh;
  width:90vw;
  margin-left: 5vw;
  margin-top: 2%;
}

.search-text{
  text-align: center;
  font-size: 250%;
  margin:0;
  margin-right: 1vw;
}

.search-input{
  width: 30vw;
  height:6vh;
  border-radius: 5px;
  border: none;
  background-image: linear-gradient(-225deg, #FFAFCC 0%, #BDE0FE 48%, #A2D2FF 100%);
  color: black;
}

.search-input::-webkit-input-placeholder{
  color: black;
}

.search-input:-ms-input-placeholder{
  color: black;
}

.search-input::placeholder{
  color: black;
}

.navbar-right{
  display: flex;
  justify-content: center;
  align-items: center;
}

.themeselect{
  margin-left: 20%;
  cursor: pointer;
}
.infobar{
  width:99%;
  display: flex;
  text-align: left;
  justify-content: left;
  border-bottom: 1px solid white;
  margin-left: 0.5%;
}
.infocontainer{
  display: flex;
  width: 100%;
  flex-direction: column;
}
.sortbuttondiv{
  width:100%;
  display: flex;
  margin: 0 auto;
  height: 10%;
}
.sortbutton{
  margin: 0 auto;
  border: none;
  background-image: linear-gradient(-225deg, #FFAFCC 0%, #BDE0FE 48%, #A2D2FF 100%);
  color: black;
  border-radius: 4%;
  width: 20%;
  margin-left: 75%;
  height: 5vh;
}
.showMoreButtonDiv{
  width:100%;
  display: flex;
  margin: 0 auto;
  height: 10%;
}

.showMoreButton{
  margin: 0 auto;
  border: none;
  background-image: linear-gradient(-225deg, #FFAFCC 0%, #BDE0FE 48%, #A2D2FF 100%);
  color: black;
  border-radius: 4%;
  width: 20%;
  height: 5vh;
  margin-top: 5vh;
  margin-bottom: 3vh;
}
.rank{
  width:5%;
  margin-right: 6%;
  margin-left: 1.5%;
  text-align: center;
}
.name{
  width: 18%;
  text-align: center;
  margin-right: 5%;
}
.ticker{
  width:13%;
  text-align: center;
}
.coinprice{
  width: 10%;
  text-align: center;
}
.marketcap{
  width:15%;
  text-align: center;
}
.percentchange24{
  width:15%;
  text-align: center;
}
.favorite{
  width: 10.5%;
  text-align: right;
}

.favoritebutton{
  width: 5%;
}
.red{
  color: #f00606;
}
.green{
  color: #11d811;
}
.coin-container{
    display: flex;
    justify-content: center;
    width:100%;
    height:100%;
}

.coin-row{
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height:12vh;
    border-bottom: 1px solid #d7d7d7;
    width:99%;
}
.coin{
    display: flex;
    align-items: center;
    width:50%;
    justify-content: space-around;
    text-align: left;
    height: 100%;   
}

.coin img{
    height:40px;
    width: 40px;
    margin-right: 10px;
}

.coin h1{
    width: 30%;
}
.coin h2{
    font-size: 175%;
}
.coin-symbol{
    width:15%;
    font-size:150%;
}
.coin-data{
    display: flex;
    text-align: center;
    justify-content: space-between;
    width:50%;
    margin:0 auto;
    height: 100%;
    font-size: 150%;
}

.coin-price{
    display: flex;
    width:25%;
    align-items: center;
}

.coin-marketcap{
    width:40%;
    display: flex;
    align-items: center;
}

.coin-percent{
    width:15%;
    display: flex;
    align-items: center;
}
.favoritebutton{
    display: flex;
    align-items: center;
    margin-right: 3%;
    height: 100%;
    width:5%;
    margin-left: 2%;
}   
.checkbox{
    max-height: 100%;
    height:100px;
    width: 100px;
}
.red{
    color: #f00606;
}
.green{
    color: #11d811;
}

.infobar{
    margin-top: 3%;
    font-size: 150%;
}

@media only screen and (max-width:1200px){
    .CryptoApp{
        font-size: 80%;
    }
    .coin h1{
        font-size: 130%
    }
    .coin img{
        height:25px;
        width: 25px;
        margin-right: 10px;
    }
}
@media only screen and (max-width:1400px){
    .coin h1{
        font-size: 130%
    }
}
@media only screen and (max-width:850px){
    .CryptoApp{
        font-size: 60%;
    }
}

@media only screen and (max-width:650px){
    .CryptoApp{
        font-size: 50%;
    }
}

@media only screen and (max-width:520px){
    .sortbutton{
        font-size:120%;
    }
    .CryptoApp{
        font-size: 45%;
    }
    .coin img{
        height:20px;
        width: 20px;
        margin-right: 10px;
    }
    .favoritebutton{
        width: 10%;
    }
    .percentchange24{
        font-size: 80%
    }
}
@media only screen and (max-width:400px){
    .CryptoApp{
        font-size: 35%;
    }
}
@media only screen and (max-width:300px){
    .CryptoApp{
        font-size: 30%;
    }
}
@media only screen and (min-width:1400px){
    .themeselect{
        height: 80px;
        width: 80px;
    }
    .search-input{
        font-size: 120%;
    }
    .sortbutton{
        font-size:140%;
        width: 15%;
        margin-left: 80%;
    }
    .coin h1{
        font-size: 150%
    }
}
@media only screen and (min-width:1550px){
    .CryptoApp{
        font-size: 150%;
    }
    .coin img{
        height:60px;
        width: 60px;
        margin-right: 10px;
    }
    .themeselect{
        height: 100px;
        width: 100px;
    }
    .search-input{
        font-size: 150%;
    }
}

@media only screen and (min-width:2000px){
    .CryptoApp{
        font-size: 170%;
    }
}
.global-container{
    display: flex;
    width: 100%;
    text-align: center;
    height: 35vh;
    margin-bottom: 2vh;
}
.global-info-left{
    width: 50%;
    text-align: center;
    justify-content: center;
    align-items: center;
    margin: auto;
    font-size: 180%;
}
.global-info-right{
    display: flex;
    width: 50%;
    justify-content: center;
}

@media only screen and (max-width:500px){
    .global-info-left{
        width: 100%;
    }
    .global-info-right{
        display: none;
    }
    .global-container{
        height: 20%;
    }
}
